import React, { useEffect, useState } from "react";
import { Modal, Button, } from "react-bootstrap";
import firebase from 'firebase'

const Basket = (props) => {
    const [items, setItems] = useState([])

    useEffect(async () => {
        const arr = []
        for (let i = 0; i < props.orders.length; i++) {
            const resp = await firebase.firestore().collection("product").doc(props.orders[i]).get()
            arr.push({ ...resp.data(), id: i + 1 })
        }
        setItems(arr)
        console.log(arr)

    }, [props.orders])
    const getItem = (index) => async () => {
        console.log(index)

        const resp = await firebase.firestore().collection("product").doc(index).get()
        console.log(resp.data())
    }
    return (
        <Modal
            {...props}
            size="lg"
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header closeButton>
                <Modal.Title id="contained-modal-title-vcenter">
                    Basket
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div>
                    {
                        items.map((p) => {
                            return (
                                <div>
                                    <h2>{p.id}</h2>
                                    <h5>{p.brendName}</h5>
                                    <img src={p.productImage} style={{height:'200px'}}/>
                                    <p>Price: {p.price}</p>
                                    <hr/>
                                </div>

                            )
                        })
                    }
                </div>
            </Modal.Body>
            <Modal.Footer>
                <Button onClick={props.onHide}>Close</Button>
            </Modal.Footer>
        </Modal>
    )
}
export default Basket